@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
#logo {
  font-family: Georgia, serif;
  font-size: 30px;
  font-weight: 900;
  color: red;
  height: 100px;
}
.wf-active #logo {
  color: #383838;
  font-family: "Playfair Display";
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
}
.wf-active #logo:hover {
  color: #2e84ff;
}
header {
  position: relative;
  margin-top: 20px;
  height: auto;
  display: flex;
  flex-direction: column;
}
.tab {
  padding: 10px;
  background: white;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  -webkit-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
}
form {
  margin-top: 25px !important;
  position: relative;
}
form .errorMsg {
  position: absolute;
  width: 100%;
  min-width: 250px;
}
form button {
  margin-left: 1%;
  position: relative;
}
form * {
  display: inline-block;
}

.heading {
  margin-top: 3%;
}

.grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: repeat(4, 1fr);
  grid-gap: 8px;
}
.grid .tab:nth-of-type(1) {
  grid-column: 1/1;
  grid-row: 1/4;
  background-image: url(/weather-app/svg/landscape.svg);
  background-position: center;
  color: white;
}
.grid .tab:nth-of-type(2) {
  grid-column: 1/1;
  grid-row: 4/4;
}
.grid .tab:nth-of-type(3) {
  grid-column: 1/1;
  grid-row: 5/5;
}
.grid .tab:nth-of-type(4) {
  grid-column: 1/1;
  grid-row: 6/6;
}
.grid .tab:nth-of-type(5) {
  grid-column: 1/1;
  grid-row: 7/7;
}

@media (min-width: 600px) {
  .grid {
    display: grid;
    width: 100%;
    min-height: 300px;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-gap: 20px;
  }
  .grid .tab:nth-of-type(1) {
    grid-column: 1/3;
    grid-row: 1/5;
    background-image: url(/svg/landscape.svg);
    background-position: center;
    color: white;
  }
  .grid .tab:nth-of-type(2) {
    grid-column: 3/3;
    grid-row: 1/1;
  }
  .grid .tab:nth-of-type(3) {
    grid-column: 3/3;
    grid-row: 2/2;
  }
  .grid .tab:nth-of-type(4) {
    grid-column: 3/3;
    grid-row: 3/3;
  }
  .grid .tab:nth-of-type(5) {
    grid-column: 3/3;
    grid-row: 4/4;
  }
}
span *:not(.tempInfo):not(.task) {
  display: inline;
}

.tab {
  color: #666666;
}

.tab:nth-of-type(1) {
  position: relative;
}
.tab:nth-of-type(1) .tempInfo, .tab:nth-of-type(1) .task {
  font-size: 16px;
  font-weight: 400;
  text-shadow: 0px 0px 5px black;
  position: absolute;
  line-height: 1.14;
}
.tab:nth-of-type(1) .tempInfo {
  font-size: 20px;
  top: 25%;
  left: 6%;
  text-shadow: 0px 0px 5px #e17f5a;
}
.tab:nth-of-type(1) .task {
  bottom: 10%;
  right: 6%;
}
.tab:nth-of-type(1) span {
  position: absolute;
  top: 10%;
  left: 6%;
  text-shadow: 0px 0px 5px #e17f5a;
}
@font-face {
  font-family: "Roboto", sans-serif;
}
/* http://meyerweb.com/eric/tools/css/reset/ 
 * v2.0 | 20110126
 * License: none (public domain) */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

button.transparent, button.blue {
  cursor: pointer;
  padding: 12px 31px;
  border-radius: 3px;
  font-size: 14px;
  font-weight: 600;
  font-family: "Roboto";
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: all 0.08s ease-in-out;
  transition: all 0.08s ease-in-out;
  -webkit-transition-property: background-color, opacity, box-shadow;
  transition-property: background-color, opacity, box-shadow;
}
button.transparent:focus, button.blue:focus {
  padding: 11px 30px;
  border: 2px solid rgba(255, 255, 255, 0);
}
button.transparent:active, button.blue:active {
  padding: 12px 31px;
}

.input label, .input label + *:not(input) {
  font-family: "Roboto";
  display: block;
  line-height: 25px;
  margin-left: 3px;
}
.input label {
  font-weight: 600;
  font-size: 14px;
}
.input label + *:not(input) {
  font-family: "Roboto";
  font-size: 14px;
}
.input input[type=submit], .input select[type=submit] {
  -webkit-text-fill-color: #515876;
  color: #515876;
}

.input input {
  font-family: "Roboto";
  display: block;
  width: 100%;
  -webkit-text-fill-color: #8a8ea0;
  border-radius: 3px;
  padding: 8px;
  z-index: 1;
  box-sizing: border-box;
  font-size: 14px;
  outline: none;
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
  -webkit-transition-property: box-shadow, opacity, background-color;
  transition-property: box-shadow, opacity, background-color;
}
.input input:focus {
  border: 2px solid #0066ff;
  box-shadow: 0 0 0 2px rgba(165, 201, 227, 0.8);
  padding: 7px;
}
.input input:focus::-webkit-input-placeholder {
  opacity: 1;
}
.input input:focus::-moz-placeholder {
  opacity: 1;
}
.input input:focus:-ms-input-placeholder {
  opacity: 1;
}
.input input:focus::-ms-input-placeholder {
  opacity: 1;
}
.input input:focus::placeholder {
  opacity: 1;
}
.input input::-webkit-input-placeholder {
  opacity: 0.6;
}
.input input::-moz-placeholder {
  opacity: 0.6;
}
.input input:-ms-input-placeholder {
  opacity: 0.6;
}
.input input::-ms-input-placeholder {
  opacity: 0.6;
}
.input input::placeholder {
  opacity: 0.6;
}
.input input[type=submit] {
  -webkit-text-fill-color: #515876;
  color: #515876;
}

.input.disabled label, .input.disabled div, .input.disabled input, .input.disabled select {
  opacity: 0.5;
  pointer-events: none;
}
.input.disabled input, .input.disabled select {
  box-shadow: none;
  border: 1px solid #cccccc;
}
.input.disabled input:focus, .input.disabled input:active, .input.disabled select:focus, .input.disabled select:active {
  padding: 8px !important;
}

* {
  box-sizing: border-box;
}
.wf-active * {
  font-family: "Roboto";
}

#root {
  margin-left: auto;
  margin-right: auto;
  padding: 0 2%;
  max-width: 1012px;
}

button.transparent {
  color: #666666;
  background-color: #ffffff;
  border: 1px solid #ebebeb;
  box-shadow: inset 0px 1px 0px white, inset 0 -1px 1px 0 #a6a6a6, 0 1px 1px 0 rgba(179, 179, 179, 0.11), 0 2px 2px 0 rgba(179, 179, 179, 0.11), 0 4px 4px 0 rgba(179, 179, 179, 0.11), 0 8px 16px 0 rgba(179, 179, 179, 0.11);
}
button.transparent:active {
  border: 1px solid #d9d9d9;
}
button.transparent:disabled {
  opacity: 0.5;
  cursor: default;
  box-shadow: none;
}
button.transparent:hover:enabled, button.transparent:focus:enabled {
  background-color: #ebebeb;
}
button.transparent:active:enabled {
  box-shadow: inset 0 -1px 1px 0 #f2f2f2;
  background-color: #ebebeb;
}
button.transparent:focus:enabled:not([data-whatintent=mouse]):not([data-whatinput=mouse]) {
  border: 2px solid #0066ff;
  box-shadow: 0 0 0 2px rgba(165, 201, 227, 0.8);
}
button.blue {
  color: white;
  background-color: #2e84ff;
  border: 1px solid #056cff;
  box-shadow: inset 0px 1px 0px #94c0ff, inset 0 -1px 1px 0 #00327b, 0 1px 1px 0 rgba(0, 61, 148, 0.11), 0 2px 2px 0 rgba(0, 61, 148, 0.11), 0 4px 4px 0 rgba(0, 61, 148, 0.11), 0 8px 16px 0 rgba(0, 61, 148, 0.11);
}
button.blue:active {
  border: 1px solid #005ce1;
}
button.blue:disabled {
  opacity: 0.5;
  cursor: default;
  box-shadow: none;
}
button.blue:hover:enabled, button.blue:focus:enabled {
  background-color: #6ba8ff;
}
button.blue:active:enabled {
  box-shadow: inset 0 -1px 1px 0 #4893ff;
  background-color: #6ba8ff;
}
button.blue:focus:enabled:not([data-whatintent=mouse]):not([data-whatinput=mouse]) {
  border: 2px solid #1a5fc7;
  box-shadow: 0 0 0 2px rgba(82, 163, 222, 0.8);
}

.input {
  width: calc(100% - 105px);
  min-width: 250px;
}
.input * {
  font-size: 14px !important;
}
.input input {
  padding: 12px !important;
}
.input input:focus {
  padding: 11px !important;
}
.input.transparent input {
  border: 1px solid #d9d9d9;
  background-color: white;
  box-shadow: 0 0px 3px #e6e6e6 inset;
  -webkit-text-fill-color: #737373;
}
.input.transparent input:hover, .input.transparent input:focus {
  background-color: white;
}
.input.transparent input:focus:not(.error) {
  border: 2px solid #0066ff;
  box-shadow: 0 0 0 2px rgba(165, 201, 227, 0.8);
  padding: 7px;
}
.input.transparent label, .input.transparent label + * {
  color: #666666;
}
.input.disabled input {
  border: 1px solid #d9d9d9;
  background-color: white;
  box-shadow: 0 0px 3px #e6e6e6 inset;
  -webkit-text-fill-color: #737373;
}
.input.disabled input:hover, .input.disabled input:focus {
  background-color: white;
}
.input.disabled input:focus:not(.error) {
  border: 2px solid #0066ff;
  box-shadow: 0 0 0 2px rgba(165, 201, 227, 0.8);
  padding: 7px;
}
.input.disabled label, .input.disabled label + * {
  color: #666666;
}
.input.error {
  color: #da2700;
}
.input.error input, .input.error select, .input.error textarea {
  border: 1px solid #ff8d74;
  box-shadow: 0 0px 3px #ffcbc0 inset;
  background-color: #fff9f8;
  -webkit-text-fill-color: #da2700;
}
.input.error input:focus, .input.error select:focus, .input.error textarea:focus {
  border: 2px solid #ff4e27;
  box-shadow: 0 0 0 2px #ffe0da;
  padding: 7px;
}
.input.error input:hover, .input.error input:focus, .input.error select:hover, .input.error select:focus, .input.error textarea:hover, .input.error textarea:focus {
  background-color: white;
}
.input.error label, .input.error label + * {
  color: #da2700;
}
.input.error input:focus {
  border: 2px solid #ff4e27 !important;
  box-shadow: 0 0 0 2px #ffe0da !important;
}
.input.error .errorMsg {
  padding: 4px 0;
}

a {
  color: #2e84ff;
  border-radius: 5px;
  padding: 0 4px;
  text-decoration: none;
  outline: none;
  border: 2px solid transparent;
  -webkit-transition: box-shadow 0.2s;
  -webkit-transition: box-shadow 0.1s;
  transition: box-shadow 0.1s;
}
a:focus {
  color: #243449;
  border: 2px solid #0066ff;
  box-shadow: 0 0 0 2px rgba(165, 201, 227, 0.8);
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
}
a:hover {
  color: #243449;
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
}

